import { inject, makeStateKey, TransferState } from "@angular/core";
import { ActivatedRouteSnapshot } from "@angular/router";
import allSessionizeData from '../../../assets/json/allSessionizeData.json';

export function scheduleDataResolver(
  route: ActivatedRouteSnapshot
): Promise<any> {
  const transferState = inject(TransferState);
  const key = makeStateKey<any>("schedule");
  const storedData = transferState.get(key, null);

  if (storedData) {
    return storedData;
  } else {
    const response = new Promise((resolve) => resolve(allSessionizeData));

    return response.then((data:any) => {
      transferState.set(key, data);

      return data;
    });
  }
}
