import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { ScheduleComponent } from '../views/schedule/schedule.component';
import { ScheduleContainerComponent } from '../views/schedule-container/schedule-container.component';
import { scheduleDataResolver } from '../resolvers/schedule.resolver';

const routes: Routes = [
  {
    path: '',
    component: ScheduleContainerComponent,
    children: [
      {
        path: '',
        pathMatch: 'full',
        component: ScheduleComponent,
        resolve: {
          schedule: scheduleDataResolver
        }
      },
    ]
  },
];

@NgModule({
  declarations: [],
  imports: [
    CommonModule,
    RouterModule.forChild(routes)
  ],
  exports: [
    RouterModule
  ]
})
export class ScheduleRoutingModule { }
