import { Component, Inject, Input, OnInit, PLATFORM_ID } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-schedule',
  templateUrl: './schedule.component.html',
})
export class ScheduleComponent implements OnInit {
  constructor(
    private activatedRoute: ActivatedRoute,
  ) {
  }

  isBrowser = false;
  jsonFile: string = 'allSessionizeData.json';
  sessionGroups = {};

  async ngOnInit() {
    const allSessionizeData = await this.activatedRoute.snapshot.data["schedule"];

    this.sessionGroups = this.groupBy(allSessionizeData.sessions, 'startsAt')
  }

  // lacking built-in Object.groupBy()
  groupBy(items:[], key:string):object {
    return items.reduce(
      (result, item) => ({
        ...result,
        [item[key]]: [
          ...(result[item[key]] || []),
          item,
        ],
      }),
      {},
    );
  }
}
