import { Component, Input, OnInit } from '@angular/core';
import { Session } from '@app/shared/interfaces/session';
import { DatePipe } from '@angular/common';

@Component({
  selector: 'app-time-block',
  templateUrl: './time-block.component.html',
})
export class TimeBlockComponent implements OnInit {
  constructor(
    private datePipe: DatePipe
  ) { }

  isOpen = true;

  @Input() sessions:Session[] = [];
  @Input() heading:string = 'Group Heading';

  ngOnInit(): void {
    this.heading = this.getConstructedHeading();
  }

  handleAccordionButtonClick():void {
    this.isOpen = !this.isOpen;
  }

  transformDate(date:Date) {
    return this.datePipe.transform(date, 'shortTime');
  }

  getConstructedHeading():string {
    const start = new Date(this.getEarliestStartDate());
    const end = new Date(this.getLatestEndDate());

    return `${this.transformDate(start)} - ${this.transformDate(end)}`;
  }

  getEarliestStartDate():number {
    const times:number[] = [];

    this.sessions.map(o => {
      times.push(Date.parse(o.startsAt));
    });

    return Math.min(...times);
  }

  getLatestEndDate():number {
    const times:number[] = [];

    this.sessions.map(o => {
      times.push(Date.parse(o.endsAt));
    });

    return Math.max(...times);
  }
}
